import NetAxios from './api'
import BigNumber from "bignumber.js";
import {inputMapType} from "../utils/max";


export class HttpServer {
    static account: string | null;

    /**
     * product
     * @param product
     * @param amount
     * @param period
     */
    static product(product: string, amount: number, period: number): Promise<any> {
        return NetAxios.get('/quote', {product, amount, period});
    }

    /**
     * ethToUsd
     */


    /**
     * sing
     * @param account
     * @param type
     * @param amount
     * @param period
     * @param product
     * @param nonce
     */
    static sign(account: string, type: number, amount: BigNumber, period: number, product: string, nonce: string): Promise<any> {
        return NetAxios.post('/sign', {
            product,
            amount,
            "cost": amount,
            type,
            period,
            nonce,
            account,
            'deadline': 1607680924304
        });
    }

    /**
     * coverClaim
     * @param nonce
     */
    static coverClaim(nonce:string): Promise<any> {
        return NetAxios.post('/cover/claim', {
            nonce,
            account:this.account
        });
    }


    /**
     * stakeBalance
     * @param balance
     */
    static stakeBalance(balance: number) {
        return NetAxios.get('/stake/balance', {
            user: this.account, balance
        });
    }


    /**
     * stakeBalance
     * @param product
     */
    static record(product: number) {
        return NetAxios.get('/cover/record', {
             product
        });
    }

    /**
     * mcr
     */
    static mcr(ev: any) {
        return NetAxios.get('/capital/mcr', {});
    }


    /**
     * withdraw
     * @param user
     * @param balance
     */
    static withdraw(balance: string) {
        return NetAxios.get('/capital/withdraw', {
            user: this.account, balance
        });
    }

    /**
     * mintReward
     */
    static mintReward(user:string) {
        return NetAxios.get('/cover/mintReward', {user,});
    }

    /**
     * amount
     */
    static amount() {
        return NetAxios.get('/cover/amount', {});
    }

    /**
     * product
     */
    static averProduct(uid: number) {
        return NetAxios.get('/product', {uid});
    }

    /**
     * stake
     * @param sign
     * @param user
     * @param params
     */
    static stake(sign: string, params: string) {
        return NetAxios.post('/stake/stake', {
            user: this.account, sign, params
        });
    }

    /**
     * stake
     * @param sign
     * @param user
     * @param params
     */
    static unstake(sign: string, params: string) {
        return NetAxios.post('/stake/unstake', {
            user: this.account, sign, params
        });
    }


    /**
     * maxAll
     * @param balance
     * @param products
     * @param target
     */
    static maxAll(balance: string, products: inputMapType, target: any) {
        return NetAxios.post('/stake/max', {
            user: this.account, balance, products, target
        });
    }


    /**
     * unstakeAndclaimAndwithdraw
     * @param url
     * @param account
     * @param nonce
     * @param amount
     * @param currency
     */
    static unstakeAndclaimAndwithdraw(url: string, account: string, nonce: string, amount: string, currency: string | null): Promise<any> {
        return NetAxios.post(url, {
            account, amount, nonce, currency
        })
    }


}
