import React from 'react';
import {HeaderWidget, FooterWidget, ButtonWidget} from "./widget/base";
import {HashRouter as Router, Switch, Route, Redirect,} from "react-router-dom";
import {CoversPage} from "./view/covers";
import styled, {ThemeProvider} from "styled-components";
import {WidthStyle} from "./widget/style/width_style";
import {themes, theme} from "./theme";
import {ThemeProvider as MuiThemeProvider} from '@material-ui/core/styles';
import {ModelProvider} from "./provider/model_provider";
import {StartMiningPage} from "./view/start_mining";
import {StartListPage} from "./view/stake_list";

import {useWallet, UseWalletProvider} from 'use-wallet'
import {useModel} from "./hook/use_model";
import {WalletConnect} from "./widget/modal/wallet_connect";
import {Web3Provider} from "./provider/web3_provider";
import {BackdropProvider} from "./provider/backdrop_provider";
import {AppConfig} from "./config";
import {Alert} from "@material-ui/lab";
import {Dialog} from "@material-ui/core";
import PubBgImg from "./assets/imgs/pub_bg.jpg"

const App: React.FC = () => {


    return (
        <Provider>
            <div className="App">
                <Page/>
            </div>
        </Provider>


    );
}


const Provider: React.FC = ({children}) => {
    return (
        <UseWalletProvider chainId={AppConfig.chainId} connectors={{
            walletconnect: {rpcUrl: AppConfig.rpcUrl[AppConfig.chainId]}
        }}>
            <ThemeProvider theme={theme}>
                <MuiThemeProvider theme={themes}>
                    <Web3Provider>
                        <ModelProvider>
                            <BackdropProvider>
                                {children}
                            </BackdropProvider>
                        </ModelProvider>
                    </Web3Provider>

                </MuiThemeProvider>

            </ThemeProvider>

        </UseWalletProvider>

    );
}


const Page: React.FC = () => {

    const {status, chainId, networkName} = useWallet();

    const [onConnect,] = useModel(<WalletConnect/>);
    const [open, setOpen] = React.useState(false);
    const handleClose = (value: string) => {
        setOpen(false);
    };
    return (<PubBg>
        {
            status === 'connected' ? <Router>
                <HeaderWidget/>
                <MainStyle>
                    <Switch>
                        <Route path="/cover/">
                            <CoversPage/>
                        </Route>
                        <Route path="/start_mining/">
                            <StartMiningPage/>
                        </Route>
                        <Route path="/start_list/">
                            <StartListPage/>
                        </Route>
                        {/*<Route path="/">
                              </Route>*/}
                        <Redirect to="/start_mining/"/>

                    </Switch>
                </MainStyle>
                <FooterWidget/>
            </Router> : <div
                style={{
                    alignItems: 'center',
                    display: 'flex',
                    flex: 1,
                    justifyContent: 'center',
                    height: '100vh'
                }}
            >
                <ButtonWidget
                    onClick={() => {

                        // @ts-ignore
                        if (window['ethereum'].networkVersion != chainId) {
                            setOpen(true);
                            return;
                        } else {
                            onConnect();
                        }
                    }}
                    text="🔓 Unlock Wallet"
                    type={"img"}
                />
                <Dialog onClose={handleClose} open={open}>
                    <Alert severity="error"> Please switch to {networkName} network!</Alert>
                </Dialog>
            </div>

        }
    </PubBg>);
};


const MainStyle = styled.div`
  ${WidthStyle.MaxWidth};
  margin: 40px auto;
`;

const PubBg = styled.div`
  background-image: url(${PubBgImg});
`
export default App;
