const ChilderWidget:React.FC = () =>{
    const {data,request} =useWait(HttpServer.amount);
    useEffect(()=>{
        request('');
    },[])


    return (
        <div style={{minHeight:"64vh"}}>
            <ListWapStyle>
                <BorderWidget>
                        <h2 />
                        <ButtonWidget to="/cover/all" text="My Policies" type="img"/>
                </BorderWidget>
                <NsuerStakingWidget data={data||{}} />
            </ListWapStyle>

            <div style={{height:'29px'}} />
            <ListWapStyle>
                <MyListWidget />

            </ListWapStyle>

        </div>
    )
}



const MyListWidget: React.FC = () => {
    const { onPresent} = useContext(ModalContext);
    return (
        <SearchListWidget  url={"/cover/list"} title={"Protocols"} sub={"search for ticker or contract address"}
                           structure={(ev) => {

                               return [
                                   {
                                       inner: ev.name,
                                       url: ev.logo.filename,
                                       color: "#FFFFFF",
                                       type: 'Imagelabel',
                                       size: 28
                                   },
                                   {
                                       inner:formatAddress(ev.address),
                                       color: "#FFFFFF",
                                   },
                                   {
                                       inner: ev.risLevel && ev.risLevel.toString(),
                                       color: "#FFFFFF",
                                       type: 'grade',
                                       grade: ev.risLevel,
                                       onClick: () => {
                                           onPresent(<ProductInformation
                                               address={ev.address}
                                               imgName={ AppConfig.urlPath+ ev.logo.filename}
                                               {...ev}
                                           />)
                                       }
                                   },
                                   {
                                       inner: getBalanceNumber(ev.coverAvailableAmount)+ " ETH",
                                       color: "#FFFFFF",
                                   },
                                   {
                                       inner:ev.premium.toFixed(4) + " ETH",
                                       color: "#FFFFFF",
                                   },
                                   {
                                       inner: '',
                                       type:null,
                                       children:  <div onClick={()=>{
                                           UrlHerf.setDara = ev;
                                       }}><ButtonWidget key={ev.id} to={"/cover/average_premium?data="+JSON.stringify({id:ev.uid})} text="Quote" type="color"/></div>

                                   }
                               ];
                           }}>
            <div>Protocol</div>
            <div>Address</div>
            <div>Security Rating</div>
            <div>Available Capacity</div>
            <div>Estimated Cost <br />(1 ETH for 30 days)</div>
            <div />
        </SearchListWidget>
    );
}







