import styled from "styled-components";
import {BorderWidget, ButtonWidget, InputWidget, SpacingWidget} from "../../base";
import React, {useCallback, useContext, useEffect, useRef} from "react";
import {useForm} from "react-hook-form";
import {ListCellWidget} from "../../base/list_cell_widget";
import {SelectWidget} from "./select_widget";
import {ModalContext} from "../../../provider/model_provider";
import {CoverModal} from "./cover_model";
import {UseUrlData} from "../../../utils/url";
import {HttpServer} from "../../../net/http_server";
import {useWait} from "../../../utils/wait_fn";
import {useSignFn} from "../../../view/covers/fn/sign_fn";
import {useWeb3} from "../../../provider/web3_provider";
import {getBalanceNumber, numberToUint256} from "../../../utils/formatBalance";
import {ProductInformationInterface} from "../../../interface/product_interface";
import { useHistory } from "react-router-dom";
export const CoverPeriodWidget: React.FC<{ urlData: ProductInformationInterface }> = ({urlData}) => {
    const {register, handleSubmit, setValue, errors, watch} = useForm();
    const {onPresent} = useContext(ModalContext);

    const watchShowAge = watch("days", 0);
    const watchAmount = watch("Amount", 0);
    const [age, setAge] = React.useState('ETH');
    const history = useHistory();



    const [signFn] = useSignFn();

    const handleLogin = useCallback(async (data: any) => {
        if (urlData === undefined) return;
        try {
            let singData = await signFn({...data, address: urlData.uid,age});
            onPresent(<CoverModal
                CellModel={urlData}
                Cover={{
                    unit: age,
                    id: urlData.uid,
                    address: urlData['address'],
                    fixedPayout: '123123',
                    days: data.days,
                    cover: data.Amount,
                    ...singData
                }}
            />)
        } catch (e) {
            console.log(e);
        }

    }, [urlData])


    const handleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
        setAge(event.target.value as string);
    };



    const historyPush = useCallback(()=> history.push('/cover/my'),[]);

    const {
        status,
        request,
        data
    } = useWait((ev: any) => HttpServer.product(urlData.uid.toString(), ev.watchAmount, ev.watchShowAge));


    useEffect(() => {
        if (!watchAmount) return;

        const item = setTimeout(async () => {
            await request({
                ...urlData,
                watchAmount: numberToUint256(watchAmount).toNumber(),
                watchShowAge,
            });
        }, 1000);
        return () => {
            clearTimeout(item);
        }

    }, [watchAmount, watchShowAge])
    return (
        <CoverPeriodWapStyle>
            <BorderWidget y={18}>
                <h2 style={{lineHeight: 1.6}}>Policy Period and Amount</h2>
            </BorderWidget>
            <InnerStyle>
                <form>
                    <InputWidget
                        setValue={(ev) => setValue(ev, urlData.minDuration)}
                        label={"Period (Min. 30 days, max. 365 days)"}
                        placeholder={"Enter the number of days to be protected"}
                        unit={"DAYS"}
                        errors={errors}
                        name={'days'}
                        buttonText={'Min'}
                        type='number'
                        inputRef={register({
                            required: 'no Enter the number is dame',
                            min: {value: urlData.minDuration, message: `Min ${urlData.minDuration} days`},
                            max: {value: urlData.maxDuration, message: `Max ${urlData.maxDuration} days`},
                        })}
                        onChange={handleSubmit(()=>{console.log('')})}

                    />
                    <p className={'tips'}>There will be 2 days waiting period, your policy will be active after</p>

                    <SpacingWidget/>

                    <WidgetWap>
                        <InputWidget
                            errors={errors}
                            inputRef={register({
                                required: 'no Enter the number is dame',
                                validate:(ev)=>{
                                    if( !(ev.toString().split("."))[1] || ev.toString().split(".")[1].length < 2){
                                        return;
                                    }
                                    return "Must be a multiple of 0.1"
                                }
                            })}
                            label={"Amount"}
                            type='number'
                            placeholder={"Enter the amount you want to be protected"}
                        >
                            <SelectWidget selectMap={['ETH']} handleChange={handleChange} value={age}/>
                        </InputWidget>


                        <ListCellWidget label={"Estimated Cost:"} last={`${(data?.list || 0).toFixed(6)} ETH`}
                                        status={status}/>

                        <SpacingWidget/>
                    </WidgetWap>
                    <ButtonList>
                        <div className={'full'}>
                            <ButtonWidget text="Get Quote" type="img" onClick={handleSubmit(handleLogin)}/>
                        </div>

                        <div className={'return'}>
                            <ButtonWidget text="Return" type="border" onClick={historyPush}/>
                        </div>

                    </ButtonList>
                </form>
            </InnerStyle>
        </CoverPeriodWapStyle>
    );
}


const ButtonList = styled.div`
display: flex;
  .return{
    width: 86px;
    margin-left: 15px;
  }
  .full{
    flex: 1;
  }
`


const InnerStyle = styled.div`
  padding: 30px;
  box-sizing: border-box;
`;

export const CoverPeriodWapStyle = styled.div`
  background-color: #161F57;
  border-radius: 0 4px 4px 4px;
  overflow: hidden;
  min-height: 430px;

  .tips {
    color: #ABBDEC;
    font-size: 12px;
  }
`;


const WidgetWap: React.FC = ({children}) => {
    return (
        <WidgetStyle>
            <div>
                {children}

            </div>
            <h5 className={''}/>
        </WidgetStyle>
    );
}

const WidgetStyle = styled.div`
  display: flex;

  > div {
    flex: 1;
  }

  > h5 {
    margin-right: 15px;
    width: 86px;
  }
`



