import React, {useCallback, useContext, useEffect, useMemo} from "react";
import {Hidden, Select} from "@material-ui/core";
import InputBase from "@material-ui/core/InputBase";
import SearchIcon from "@material-ui/icons/Search";
import styled from "styled-components";
import {CoversStyle} from "../covers/style";
import {Subject, interval} from 'rxjs';
import {debounce} from 'rxjs/operators';
import {SearchContext} from "./search_list";
import {SelectWidget} from "../covers/average/select_widget";

export const ListTopWidget: React.FC = ({children}) => {
    return (
        <TopStyle top={11}>
            {children}
        </TopStyle>
    );
}

export interface ListHeaderSeachInterface {
    title?: string,
    sub?: string,
    isShowSearch?: boolean,
    select?: {[key:string]:string}
}

export const ListHeader: React.FC<ListHeaderSeachInterface> = ({
                                                                   title,
                                                                   sub,
                                                                   isShowSearch,
                                                                   select,
                                                               }) => {

    const {setIsHide, setListCellList, listCellList} = useContext(SearchContext);

    const onDebounce = new Subject();
    const [sort, setSort] = React.useState('');

    const handleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
        if(!select) return;
        let value:any = event.target.value;
        let key = select[value];
        let _list = listCellList.sort((a, b) => b[key]- a[key]);
        setListCellList([..._list]);
        setSort(event.target.value as string);
    };


    useEffect(() => {
        const subscription = onDebounce.pipe(debounce((ev) => interval(500))).subscribe({
            next: (value: any) => {
                setIsHide(value);
            }
        })
        return () => {
            subscription.unsubscribe()
        }
    }, [onDebounce])


    return (
        <ListHeaderStyle top={20}>
            <h2> {title}
                <Hidden smDown>
                    <span>{sub}</span>
                </Hidden>
            </h2>
            <SelectWapStyle>
                {
                    select ? <SelectWidget
                        selectMap={Object.keys(select)}
                        handleChange={handleChange}
                        value={sort}
                    /> : <div/>
                }
            </SelectWapStyle>

            {
                isShowSearch ? <SearchStyle>
                    <InputBase onChange={(ev) => onDebounce.next(ev.target.value)}/>
                    <SearchIcon/>
                </SearchStyle> : <></>
            }


        </ListHeaderStyle>
    );
}


const SelectWapStyle = styled.div`
  width: auto;

  .MuiSelect-select.MuiSelect-select {
    color: white;
  }

`

const SearchStyle = styled.div`
  display: flex;
  align-items: center;
  background-color: #162C75;
  width: 30%;
  flex: none !important;
  border-radius: 5px;
  padding: 0 10px;
  color: white;

  .MuiInputBase-input {
    color: white;
  }

`;

const ListHeaderStyle = styled.div`
  ${() => CoversStyle.ListCellStyle};
  background: #161F57;
  display: flex;
  justify-content: space-between;

  h2 {
    font-size: 18px;
    font-weight: 400;
    color: #FFFFFF;
    display: flex;
    align-items: center;

    span {
      font-size: 14px;
      font-weight: 400;
      color: #9FAAED;
      line-height: 1;
      display: block;
      margin-left: 10px;
    }
  }
`;


const TopStyle = styled.div`
  display: flex;
  justify-content: space-between;
  z-index: 10;
  position: relative;
  ${() => CoversStyle.ListCellStyle};
  background: #1A2460;

  div {
    font-size: 14px;
    font-weight: 400;
    color: #8390B2;
  }
`;

