export const StartManage = createContext<ManageInterface>({
    refresh: '',
    setRefresh: () => null,
    inputMap: {},
    setInputMap: () => null,
    setList: () => null,
    list: {},
});

interface ManageInterface {
    refresh: string | number,
    setRefresh: Function,
    inputMap: inputMapType,
    setInputMap: Function,
    list: any,
    setList: (ev: any) => void,

}


const MsgItemWidgte: React.FC<{ label: string, value: number | string, unit?: string }> = ({
                                                                                               label,
                                                                                               value = 0,
                                                                                               unit = 'Nsure',
                                                                                               children
                                                                                           }) => {
    return (
        <MsgItemStyle>
            <h2>{label}</h2>
            <h3><span>{getBalanceNumber(value)} {unit}</span></h3>
            <div className={'button_img'}>
                {children}
            </div>
        </MsgItemStyle>
    );
}

