import React, {useMemo} from "react";
import styled, {css} from "styled-components";
import {Link} from "react-router-dom";
import BgImage from "../../assets/imgs/button_img.jpg"
import {Button,} from "@material-ui/core";

export interface ButtonInterface {
    type:'img'|'border'|'color'|'submit',
    text:string,
    onClick?:(ev:any) => void,
    to?:string,
}




export const ButtonWidget: React.FC<ButtonInterface> = ({text,type,onClick,to}) => {
    const ButtonClider = useMemo(()=>{
        if(to){
            return <StyledLink to={to}> {text}</StyledLink>
        }else {
            return  text
        }

    },[to,text],);
    return (
        <ButtonStyle type={type} onClick={onClick}>
            {ButtonClider}
        </ButtonStyle>
    );
}




const StyledLink = styled(Link)`
  align-items: center;
  color: inherit;
  justify-content: center;
  margin: 0;
  padding: 0;
  text-decoration: none;
  width: 100%;
  height: 100%;
  display: flex;
`;

const ButtonStyle = styled.div<{ type:'img'|'border'|'color'|'submit',}>`
border-radius: 4px;
padding: 0 30PX;
height: 40px;
font-size: 16px;
line-height: 1;
color: white;
cursor:pointer;
font-weight: bold;
overflow: hidden;
${({type})=> {
    let _style;
    switch (type){
        case 'submit':
        case 'img':
            _style = ButtonImage;
            break;
        case 'border':
            _style = ButtonBorder;
            break;
        case 'color':
            _style = ButtonColor;
            break;
    }
    return _style;
}};
width: auto;
display: flex;
justify-content: center;
align-items: center;




`

const ButtonImage = css`
//background: linear-gradient(165deg, #123DB5 0%, #5A42EC 100%);
background-repeat: no-repeat;
background-size:cover;
background-position: center;
//box-shadow: 0 0 32px 0 rgba(45, 70, 146, 0.2);
border: 1px solid transparent;

  background-image:   url(${BgImage}),linear-gradient( to bottom, rgba(51, 92, 180, 1),rgba(28, 38, 98, 1)); /* 模拟渐变边框 */

  //border: 1px solid transparent;
  border-radius: 4px; /* 圆角属性测试 */
  background-clip: padding-box, border-box;
  background-origin: border-box;
   

`
const ButtonColor = css`



font-size: 14px;
font-weight: 600;
  background-image: linear-gradient(#162C75,#162C75),linear-gradient( to bottom, rgba(51, 92, 180, 1),rgba(28, 38, 98, 1)); /* 模拟渐变边框 */

  border: 1px solid transparent;
  border-radius: 4px; /* 圆角属性测试 */
  background-clip: padding-box, border-box;
  background-origin: border-box;
`
const ButtonBorder = css`

background: transparent;

//border-image: linear-gradient(0deg, red, #203778) 10 10;
   border: 1px solid rgba(25, 145, 235, 1);
font-size: 14px;
font-weight: 600;
color: rgba(39, 101, 166, 1);
//color: #FFFFFF;
clip-path: inset(0 round 4px);
`







