import {useWeb3} from "../provider/web3_provider";
import {useCallback} from "react";
import {Contract} from "web3-eth-contract";
import {useSendFn} from "../utils/web3_send";
import {numberToUint256} from "../utils/formatBalance";


export const useConvert = (contract: Contract | undefined, address: string) => {


    const handleSendFn = useSendFn(address);
    const handleConvert = useCallback(async (amount: string) => {
        if(!contract) return ;
        let method = contract.methods.convert(amount);
       let data =  await handleSendFn(method, amount);
       return data.transactionHash
    }, [contract]);

    return handleConvert

}


export const useExit = (contract: Contract | undefined, address: string) => {


    const handleSendFn = useSendFn(address);
    const handleExit = useCallback(async (amount: string) => {
        if(!contract) return ;
        let method = contract.methods.exit(amount);
       let data =  await handleSendFn(method);
       return data.transactionHash
    }, [contract]);

    return handleExit

}


