import {useCallback, useContext} from "react";
import NetAxios from "../../net/api";
import {SearchContext} from "./search_list";
import {HttpServer} from "../../net/http_server";


export const useSearchFn = () => {
    const {setListCellList, path, setLoading, onGetData,params, setPaginationModel, paginationModel} = useContext(SearchContext);

    const faceCallBack = useCallback(async (page: number) => {

        try {
            const data: any = await NetAxios.get(path, {
                user: HttpServer.account,
                ...params,
                item: 20,
                page,
            });
            setListCellList(data['list'] || data);
            onGetData&&onGetData(data['list'] || data);
            setLoading(0);
            if (!paginationModel.count) {
                setPaginationModel(
                    {
                        page: Math.ceil((data['count'] || 1) / 20),
                        count: data['count']
                    }
                );
            }


        } catch (e) {
            console.log(e);
        }

    }, [setLoading, setListCellList, path]);

    return [faceCallBack];
}
