import React, {createContext, ReactNode, useCallback, useState} from "react";
import {ListHeader, ListHeaderSeachInterface, ListTopWidget} from "./list_header";
import {CellStructure, HttpListMain} from "./http_list";
import {Pagination} from '@material-ui/lab';
import styled from "styled-components";
import {ButtonWidget} from "../base";

interface SearchListInterface extends ListHeaderSeachInterface {
    structure: (ev: any) => CellStructure[],
    url: string,
    isShowHeader?: boolean,
    isShowPagination?: boolean,
    params?: { [key: string]: any },
    confirm?:(ev:SearchInterfacer) => void,
    refresh?:string|number,
    onGetData?:(ev:any) => void
}

export const SearchListWidget: React.FC<SearchListInterface> = ({
                                                                    sub = '',
                                                                    title = '',
                                                                    structure,
                                                                    url,
                                                                    confirm,
                                                                    isShowSearch = true,
                                                                    isShowHeader = true,
                                                                    isShowPagination = false,
                                                                    params,
                                                                    select,
                                                                    refresh,
                                                                    onGetData,
                                                                    children
                                                                }) => {

    const [listCellList, setListCellList] = useState([]);
    const [pagination, setPaginationModel] = useState<PaginationModel>({
        page: 1,
        count: 0
    });
    const [loading, setLoading] = useState(1);
    const [path, setPath] = useState(url);
    const [isHide, setIsHide] = useState('');

    return (
        <SearchContext.Provider value={{
            path,
            setPath,
            listCellList,
            setListCellList,
            loading, setLoading, params,
            paginationModel: pagination,
            setPaginationModel: setPaginationModel,
            isHide, setIsHide,refresh,onGetData
        }}>
            {
                isShowHeader ? <ListHeader title={title} sub={sub} select={select} isShowSearch={isShowSearch}/> : ''
            }
            <ListTopWidget>{children}</ListTopWidget>
            <HttpListMain isShowHeader={isShowHeader} isShowSearch={isShowSearch} structure={structure}/>
            <PaginationStyle>
                {

                    isShowPagination && pagination.page ? <Pagination count={pagination.page} color="primary"
                                                                      variant="outlined"
                                                                      onChange={(ev, index) => {

                                                                          setPaginationModel({
                                                                              ...pagination,
                                                                              page: index
                                                                          });
                                                                      }
                                                                      } shape="rounded"/> : ''

                }

                {
                    confirm&& <ButtonStyle><ButtonWidget text={'Confirm'} type={"img"} onClick={() => {
                        confirm({
                            path,
                            setPath,
                            listCellList,
                            setListCellList,
                            loading, setLoading, params,
                            paginationModel: pagination,
                            setPaginationModel: setPaginationModel,
                            isHide, setIsHide
                        })
                    }}/></ButtonStyle>
                }


            </PaginationStyle>


        </SearchContext.Provider>
    );
}


export const SearchContext = createContext<SearchInterfacer>({
    path: '',
    isHide: '',
    setPath: () => {
    },
    listCellList: [],
    setListCellList: () => {
    },
    loading: 1,
    setLoading: () => {
    },
    setPaginationModel: () => {
    },
    setIsHide: () => {
    },
    paginationModel: {
        page: 1
    },
});

export interface SearchInterfacer {
    path: string,
    listCellList: any[],
    loading: number,
    params?: { [key: string]: any },
    count?: number,
    setListCellList: Function,
    setPaginationModel: Function,
    paginationModel: PaginationModel,
    setLoading: Function,
    setPath: Function,
    isHide: string,
    setIsHide: Function,
    refresh?:string|number,
    onGetData?:Function,
}


type PaginationModel = {
    page: number,
    count?: number
}


const PaginationStyle = styled.div`
  display: flex;
  justify-content: flex-end;

  > .MuiPagination-root {
    padding: 10px 5%;
  }
`

const ButtonStyle = styled.div`
  display: flex;
  padding: 30px;
  box-sizing: border-box;
  justify-content: flex-end;

`
