import {HttpStatus} from "../../utils/wait_fn";
import React, {useEffect, useState} from "react";
import {CircularProgress, Divider} from "@material-ui/core";
import styled from "styled-components";
import {useModel} from "../../hook/use_model";
import {ButtonWidget} from "../base/button";
import successImg from '../../assets/imgs/success.png'
import {useWeb3} from "../../provider/web3_provider";
import {useWallet} from "use-wallet";
import {AppConfig} from "../../config";

export const TransactionModal: React.FC<{ txHash: any, status: HttpStatus, onDismiss?: any }> = ({
                                                                                                    txHash,
                                                                                                    children,
                                                                                                    onDismiss,
                                                                                                    status
                                                                                                }) => {

    const [widget, setWidget] = useState<any>(children);

    useEffect(() => {
        if (status == HttpStatus.failure) {
            if (txHash.err.code && txHash.err.code === 4001) {
                onDismiss()
            }
            setWidget(<ErrWidget msg={txHash.err.message} />)
        }
        if (status == HttpStatus.wait) {
            setWidget(<WapStyle><LoadingWidget/></WapStyle>)
        }
        if (status == HttpStatus.success) {
            setWidget(<SuccessWidget hash={txHash}/>)
        }


    }, [status])
    return (
        <div>
            {widget}
        </div>
    );
}


const SuccessWidget: React.FC<{hash:string}> = ({hash}) => {
    const [onDismisss, onDismiss] = useModel(<div/>);
    const {chainId} = useWallet();
    return (
        <WapStyle>
            <span> </span>

            <img src={successImg} alt=""/>

            <LoadingStyle>
                <h2>Transaction Submitted</h2>

                <a href={AppConfig.etherscan[chainId || 42] + hash} target="_blank">View on Etherscan</a>
                <ButtonWidget text={'Close'} type={"img"} onClick={onDismiss}/>

            </LoadingStyle>
        </WapStyle>
    );
}

const LoadingWidget: React.FC = () => {
    return (
        <WapStyle>
            <span> </span>
            <CircularProgress size={100} color="primary"/>
            <LoadingStyle>
                <h2>Waiting For Confirmation</h2>
                <h4>Confirm this transaction in your wallet</h4>
            </LoadingStyle>
        </WapStyle>
    );
}


const ErrWidget:React.FC<{msg:string}> = ({msg}) =>{
    useEffect(()=>{console.log(msg)},[])
    return (
        <WapStyle>
            <LoadingStyle>
                {/*<h2>{msg}</h2>*/}
                <h2>Err</h2>
            </LoadingStyle>
        </WapStyle>
    );
}

const LoadingStyle = styled.div`
  text-align: center;
  margin-top: 20px;

  h2 {
    font-size: 18px;
    font-weight: 600;
    color: #FFFFFF;
  }

  h3,a {
    font-size: 14px;
    font-weight: 400;
    color: #FFFFFF;
    padding: 10px 0;
    display: block;
  }

  h4 {
    font-size: 14px;
    font-weight: 400;
    color: #ABBDEC;
  }
`

const WapStyle = styled.div`
  width: 100vw;
  max-width: 430px;
  background-color: rgba(22, 31, 87, 1);
  border-radius: 5px;
  height: 375px;
  display: flex;
  flex-flow: column;
  //justify-content: center;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 20px;
  box-sizing: border-box;


`;
